main {
  position: relative;
}
main:after {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
  visibility: hidden;
  opacity: 0;
  transition: 0.3s ease-in-out;
  z-index: 9;
}
main.overlay {
  overflow: hidden;
}
main.overlay:after {
  visibility: visible;
  opacity: 1;
}

.scrollbar {
  padding-left: 10px;
  overflow-y: auto;
  cursor: s-resize;
}
.scrollbar::-webkit-scrollbar-track {
  border-radius: 3px;
  background-color: #F5F5F5;
  opacity: 0.1;
  cursor: pointer;
}
.scrollbar::-webkit-scrollbar {
  width: 4px;
  background-color: #F5F5F5;
}
.scrollbar::-webkit-scrollbar-thumb {
  border-radius: 3px;
  background-color: rgba(71, 83, 100, 0.4);
}

.back-to-top {
  position: fixed;
  bottom: 47px;
  right: 0;
  background-color: #000;
  padding: 3px 10px;
  transform: rotate(-90deg) translateY(75px);
  color: #fff;
  height: 30px;
  padding-right: 30px;
  opacity: 0.2;
  font-size: 14px;
  font-weight: 500;
  text-decoration: none;
  transition: 0.5s ease-in-out;
  z-index: 99;
}
.back-to-top:hover, .back-to-top:focus {
  color: #ffffff;
  opacity: 1;
  text-decoration: none;
}
.back-to-top i {
  transform: rotate(90deg) translateX(-15px);
  position: absolute;
  top: 13.5px;
  right: 6px;
  display: block;
  font-size: 20px;
}
.back-to-top.show {
  transform: rotate(-90deg) translateY(45px);
}

.reset-link-popup {
